'use client';

import { useEffect, useState } from 'react';
import axios from 'axios';

// ----------------------------------------------------------------------
// [ÎÇ¥Î∂Ä Ïª¥Ìè¨ÎÑåÌä∏] Î≥ÑÏ†ê ÌëúÏãú (SVG)
// ----------------------------------------------------------------------
const StarRating = ({ rating, size = 'md' }: { rating: number, size?: 'sm' | 'md' }) => {
  const starClass = size === 'sm' ? "w-4 h-4" : "w-6 h-6";
  
  return (
    <div className="flex items-center">
      {[1, 2, 3, 4, 5].map((index) => (
        <svg
          key={index}
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 24 24"
          fill={index <= Math.round(rating) ? "#FBBF24" : "#E5E7EB"} // ÎÖ∏ÎûÄÏÉâ or ÌöåÏÉâ
          className={starClass}
        >
          <path
            fillRule="evenodd"
            d="M10.788 3.21c.448-1.077 1.976-1.077 2.424 0l2.082 5.007 5.404.433c1.164.093 1.636 1.545.749 2.305l-4.117 3.527 1.257 5.273c.271 1.136-.964 2.033-1.96 1.425L12 18.354 7.373 21.18c-.996.608-2.231-.29-1.96-1.425l1.257-5.273-4.117-3.527c-.887-.76-.415-2.212.749-2.305l5.404-.433 2.082-5.005z"
            clipRule="evenodd"
          />
        </svg>
      ))}
    </div>
  );
};

// ----------------------------------------------------------------------
// [ÎÇ¥Î∂Ä Ïª¥Ìè¨ÎÑåÌä∏] Í≤∞Ï†ú ÌôïÏù∏ Î™®Îã¨
// ----------------------------------------------------------------------
function PaymentModal({ isOpen, onClose, onConfirm, caravanName, startDate, endDate, totalPrice, isProcessing }: any) {
  if (!isOpen) return null;
  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50 animate-fade-in">
      <div className="bg-white rounded-lg shadow-xl w-full max-w-md p-6 m-4">
        <div className="flex justify-between items-center mb-4 border-b pb-3">
          <h3 className="text-xl font-bold text-gray-900">Í≤∞Ï†ú ÌôïÏù∏</h3>
          <button onClick={onClose} className="text-gray-400 hover:text-gray-600">‚úï</button>
        </div>
        <div className="space-y-4">
          <div className="bg-gray-50 p-4 rounded-md">
            <p className="text-sm text-gray-500 mb-1">ÏòàÏïΩ Ïπ¥ÎùºÎ∞ò</p>
            <p className="font-bold text-gray-800">{caravanName}</p>
          </div>
          <div className="bg-gray-50 p-4 rounded-md">
            <p className="text-sm text-gray-500 mb-1">ÏùºÏ†ï</p>
            <p className="font-medium text-gray-800">{startDate} ~ {endDate}</p>
          </div>
          <div className="bg-indigo-50 p-4 rounded-md flex justify-between items-center border border-indigo-100">
            <p className="text-indigo-700 font-medium">Ï¥ù Í≤∞Ï†ú Í∏àÏï°</p>
            <p className="text-2xl font-bold text-indigo-700">{totalPrice.toLocaleString()}Ïõê</p>
          </div>
        </div>
        <div className="mt-8 flex gap-3">
          <button onClick={onClose} className="flex-1 px-4 py-3 border rounded-md text-gray-700 font-medium hover:bg-gray-50" disabled={isProcessing}>Ï∑®ÏÜå</button>
          <button onClick={onConfirm} disabled={isProcessing} className="flex-1 px-4 py-3 bg-indigo-600 text-white rounded-md font-bold hover:bg-indigo-700 shadow-md">
            {isProcessing ? 'Ï≤òÎ¶¨ Ï§ë...' : 'Í≤∞Ï†úÌïòÍ∏∞'}
          </button>
        </div>
      </div>
    </div>
  );
}

// ----------------------------------------------------------------------
// [Î©îÏù∏] CaravanDetailPage
// ----------------------------------------------------------------------

interface Caravan {
  id: string;
  name: string;
  description: string;
  location: string;
  pricePerDay: number;
  capacity: number;
  images: string[];
  host: { name: string; id: string };
}

interface Review {
  id: string;
  rating: number;
  comment: string;
  createdAt: string;
  author: { name: string; profilePicture: string | null };
}

export default function CaravanDetailPage() {
  const [id, setId] = useState<string>('');
  const [caravan, setCaravan] = useState<Caravan | null>(null);
  const [reviews, setReviews] = useState<Review[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  
  // ÌÉ≠ ÏÉÅÌÉú Í¥ÄÎ¶¨ ('info': ÏÉÅÏÑ∏Ï†ïÎ≥¥, 'reviews': Ïù¥Ïö©ÌõÑÍ∏∞)
  const [activeTab, setActiveTab] = useState<'info' | 'reviews'>('info');

  // ÏòàÏïΩ Í¥ÄÎ†® ÏÉÅÌÉú
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [processing, setProcessing] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);

  // ID Ï∂îÏ∂ú Î∞è Ï¥àÍ∏∞ ÌÉ≠ ÏÑ§Ï†ï
  useEffect(() => {
    if (typeof window !== 'undefined') {
      const pathSegments = window.location.pathname.split('/');
      const caravanId = pathSegments.pop() || pathSegments.pop();
      if (caravanId) setId(caravanId);

      // URL Ìï¥ÏãúÍ∞Ä #reviewsÎ©¥ ÌõÑÍ∏∞ ÌÉ≠ ÌôúÏÑ±Ìôî
      if (window.location.hash === '#reviews') {
        setActiveTab('reviews');
      }
    }
  }, []);

  // Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞
  useEffect(() => {
    if (!id) return;
    const fetchData = async () => {
      try {
        setLoading(true);
        // 1. Ïπ¥ÎùºÎ∞ò Ï†ïÎ≥¥ Ï°∞Ìöå
        const caravanRes = await axios.get(`http://127.0.0.1:3001/api/caravans/${id}`);
        setCaravan(caravanRes.data);

        // 2. ÌõÑÍ∏∞ Î™©Î°ù Ï°∞Ìöå
        try {
          const reviewsRes = await axios.get(`http://127.0.0.1:3001/api/reviews/caravan/${id}`);
          setReviews(reviewsRes.data);
        } catch (reviewErr) {
          console.warn("Î¶¨Î∑∞Î•º Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§:", reviewErr);
          setReviews([]);
        }

      } catch (err: any) {
        console.error("Îç∞Ïù¥ÌÑ∞ Î°úÎî© Ïã§Ìå®:", err);
        if (err.response?.status === 404) {
          setError('Ï°¥Ïû¨ÌïòÏßÄ ÏïäÎäî Ïπ¥ÎùºÎ∞òÏûÖÎãàÎã§.');
        } else {
          setError('Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§. Î∞±ÏóîÎìú ÏÑúÎ≤ÑÎ•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.');
        }
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [id]);

  // ‚úÖ [Ï∂îÍ∞Ä] Î°úÎî© ÏôÑÎ£å ÌõÑ Ìï¥Ïãú(#reviews)Í∞Ä ÏûàÏúºÎ©¥ Ìï¥Îãπ ÏúÑÏπòÎ°ú ÏûêÎèô Ïä§ÌÅ¨Î°§
  useEffect(() => {
    if (!loading && typeof window !== 'undefined' && window.location.hash === '#reviews') {
      const element = document.getElementById('reviews-section');
      if (element) {
        setTimeout(() => {
          element.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }, 100);
      }
    }
  }, [loading]);

  // ÌèâÏ†ê Í≥ÑÏÇ∞
  const averageRating = reviews.length > 0
    ? reviews.reduce((acc, curr) => acc + curr.rating, 0) / reviews.length
    : 0;

  // Í∞ÄÍ≤© Í≥ÑÏÇ∞
  const calculateTotal = () => {
    if (!caravan || !startDate || !endDate) return 0;
    const start = new Date(startDate);
    const end = new Date(endDate);
    const diffTime = end.getTime() - start.getTime();
    if (diffTime <= 0) return 0;
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24)); 
    return diffDays * caravan.pricePerDay;
  };

  const handleOpenModal = () => {
    const userStr = localStorage.getItem('user');
    if (!userStr) {
      alert('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
      if (typeof window !== 'undefined') window.location.href = `${window.location.origin}/auth/login`;
      return;
    }
    if (!startDate || !endDate) return alert('Ï≤¥ÌÅ¨Ïù∏ Î∞è Ï≤¥ÌÅ¨ÏïÑÏõÉ ÎÇ†ÏßúÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.');
    if (calculateTotal() <= 0) return alert('Ïò¨Î∞îÎ•∏ ÎÇ†ÏßúÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.');
    setIsModalOpen(true);
  };

  const handlePaymentConfirm = async () => {
    setProcessing(true);
    try {
      const userStr = localStorage.getItem('user');
      if (!userStr) throw new Error('Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥ ÏóÜÏùå');
      const user = JSON.parse(userStr);

      const payload = {
        caravanId: id,
        guestId: user.id,
        startDate: new Date(startDate).toISOString(),
        endDate: new Date(endDate).toISOString(),
        totalPrice: calculateTotal(), 
      };

      await axios.post('http://127.0.0.1:3001/api/reservations', payload);
      setIsModalOpen(false);
      alert('üéâ ÏòàÏïΩ Ïã†Ï≤≠Ïù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§! (Ìò∏Ïä§Ìä∏ ÏäπÏù∏ ÎåÄÍ∏∞)');
      if (typeof window !== 'undefined') window.location.href = `${window.location.origin}/my/reservations`;
    } catch (err: any) {
      alert(`ÏòàÏïΩ Ïã§Ìå®: ${err.response?.data?.message || err.message}`);
    } finally {
      setProcessing(false);
    }
  };

  if (loading) return <div className="p-20 text-center text-gray-500">Î°úÎî© Ï§ë...</div>;
  
  if (error) return (
    <div className="p-20 text-center">
      <h2 className="text-2xl font-bold text-gray-800 mb-4">Ïò§Î•ò Î∞úÏÉù</h2>
      <p className="text-red-500 mb-6">{error}</p>
      <a href="/caravans" className="px-6 py-3 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition">
        Î™©Î°ùÏúºÎ°ú ÎèåÏïÑÍ∞ÄÍ∏∞
      </a>
    </div>
  );

  if (!caravan) return <div className="p-10 text-center">Ïπ¥ÎùºÎ∞òÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.</div>;

  const totalPrice = calculateTotal();
  const mainImage = (caravan.images?.[0] && caravan.images[0].startsWith('http'))
    ? caravan.images[0]
    : `https://via.placeholder.com/1200x600?text=${encodeURIComponent(caravan.name)}`;

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-10">
      <PaymentModal 
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onConfirm={handlePaymentConfirm}
        caravanName={caravan.name}
        startDate={startDate}
        endDate={endDate}
        totalPrice={totalPrice}
        isProcessing={processing}
      />

      {/* Ïù¥ÎØ∏ÏßÄ ÏÑπÏÖò (Ìï≠ÏÉÅ Î≥¥ÏûÑ) */}
      <div className="w-full h-96 bg-gray-200 rounded-xl overflow-hidden mb-8 shadow-md">
        <img src={mainImage} alt={caravan.name} className="w-full h-full object-cover" />
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
        
        {/* Ï¢åÏ∏°: Ï†ïÎ≥¥ Î∞è ÌÉ≠ ÏòÅÏó≠ */}
        {/* ‚úÖ [ÏàòÏ†ïÎê®] ÌõÑÍ∏∞ ÌÉ≠Ïùº ÎïåÎäî Ï¢åÏ∏° ÏòÅÏó≠ÏùÑ ÎÑìÍ≤å(3Ïπ∏ Î™®Îëê) ÏÇ¨Ïö©ÌïòÍ≥†, ÏÉÅÏÑ∏ Ï†ïÎ≥¥Ïùº ÎïåÎäî 2Ïπ∏ ÏÇ¨Ïö© */}
        <div className={activeTab === 'reviews' ? "lg:col-span-3" : "lg:col-span-2"}>
          
          {/* ÏÉÅÎã® Ï†úÎ™© Î∞è ÌèâÏ†ê (Î™®Îì† ÌÉ≠ Í≥µÌÜµ) */}
          <div>
            <div className="flex justify-between items-start">
              <h1 className="text-3xl font-bold text-gray-900 mb-2">{caravan.name}</h1>
              {/* ÌèâÍ∑† Î≥ÑÏ†ê */}
              {reviews.length > 0 && (
                <div className="flex items-center bg-yellow-50 px-3 py-1 rounded-lg">
                  <span className="text-yellow-500 mr-1">‚òÖ</span>
                  <span className="font-bold text-gray-800">{averageRating.toFixed(1)}</span>
                  <span className="text-gray-500 text-sm ml-1">({reviews.length} reviews)</span>
                </div>
              )}
            </div>
            <p className="text-gray-500 flex items-center mt-1">üìç {caravan.location}</p>
          </div>

          {/* ÌÉ≠ Î≤ÑÌäº */}
          <div className="flex border-b border-gray-200 mt-8 mb-6">
            <button
              onClick={() => setActiveTab('info')}
              className={`flex-1 pb-3 text-center text-sm font-bold transition-colors border-b-2 ${
                activeTab === 'info' 
                  ? 'border-indigo-600 text-indigo-600' 
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              ÏÉÅÏÑ∏ Ï†ïÎ≥¥
            </button>
            <button
              onClick={() => setActiveTab('reviews')}
              className={`flex-1 pb-3 text-center text-sm font-bold transition-colors border-b-2 ${
                activeTab === 'reviews' 
                  ? 'border-indigo-600 text-indigo-600' 
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              Ïù¥Ïö© ÌõÑÍ∏∞ ({reviews.length})
            </button>
          </div>

          {/* ‚úÖ ÌÉ≠ ÎÇ¥Ïö©: ÏÉÅÏÑ∏ Ï†ïÎ≥¥ */}
          {activeTab === 'info' && (
            <div className="space-y-10 animate-fade-in">
              <div>
                <h2 className="text-xl font-bold text-gray-900 mb-4">Ïπ¥ÎùºÎ∞ò ÏÜåÍ∞ú</h2>
                <p className="text-gray-700 whitespace-pre-line leading-relaxed">{caravan.description}</p>
              </div>

              <div className="border-t pt-6">
                <h2 className="text-xl font-bold text-gray-900 mb-4">Ìò∏Ïä§Ìä∏ Ï†ïÎ≥¥</h2>
                <div className="flex items-center">
                  <div className="w-12 h-12 bg-indigo-100 rounded-full flex items-center justify-center text-indigo-600 font-bold">
                    {caravan.host?.name?.[0] || 'H'}
                  </div>
                  <div className="ml-4">
                    <p className="text-gray-900 font-medium">{caravan.host?.name || 'Ïïå Ïàò ÏóÜÏùå'}</p>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* ‚úÖ ÌÉ≠ ÎÇ¥Ïö©: Ïù¥Ïö© ÌõÑÍ∏∞ */}
          {activeTab === 'reviews' && (
            <div id="reviews-section" className="animate-fade-in">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-bold text-gray-900">
                  Ïù¥Ïö© ÌõÑÍ∏∞ <span className="text-gray-500 font-normal text-base">({reviews.length}Í∞ú)</span>
                </h2>
                <button 
                  onClick={() => setActiveTab('info')}
                  className="text-sm text-indigo-600 hover:text-indigo-800 font-medium"
                >
                  ‚Üê ÏÉÅÏÑ∏ Ï†ïÎ≥¥Î°ú ÎèåÏïÑÍ∞ÄÍ∏∞
                </button>
              </div>
              
              {reviews.length === 0 ? (
                <div className="text-center py-10 bg-gray-50 rounded-lg text-gray-500">
                  ÏïÑÏßÅ ÏûëÏÑ±Îêú ÌõÑÍ∏∞Í∞Ä ÏóÜÏäµÎãàÎã§.
                </div>
              ) : (
                <div className="space-y-4">
                  {reviews.map((review) => (
                    <div key={review.id} className="bg-white border border-gray-100 rounded-xl p-5 shadow-sm hover:shadow-md transition-shadow">
                      <div className="flex justify-between items-start mb-3">
                        <div className="flex items-center gap-3">
                          <div className="w-10 h-10 bg-gray-100 rounded-full flex items-center justify-center overflow-hidden">
                            {review.author.profilePicture ? (
                              <img src={review.author.profilePicture} alt={review.author.name} className="w-full h-full object-cover" />
                            ) : (
                              <span className="text-gray-500 font-bold">{review.author.name[0]}</span>
                            )}
                          </div>
                          <div>
                            <p className="font-bold text-gray-900">{review.author.name}</p>
                            <p className="text-xs text-gray-400">{new Date(review.createdAt).toLocaleDateString()}</p>
                          </div>
                        </div>
                        <StarRating rating={review.rating} size="sm" />
                      </div>
                      <p className="text-gray-700 text-sm leading-relaxed pl-1">{review.comment}</p>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}
        </div>

        {/* ‚úÖ [ÏàòÏ†ïÎê®] Ïö∞Ï∏°: ÏòàÏïΩ ÏúÑÏ†Ø (ÏÉÅÏÑ∏ Ï†ïÎ≥¥ ÌÉ≠Ïùº ÎïåÎßå Î≥¥ÏûÑ) */}
        {activeTab === 'info' && (
          <div className="lg:col-span-1">
            <div className="bg-white p-6 rounded-xl shadow-lg border border-gray-200 sticky top-24">
              <div className="flex justify-between items-end mb-6">
                <span className="text-2xl font-bold text-gray-900">{caravan.pricePerDay.toLocaleString()}Ïõê</span>
                <span className="text-gray-500 mb-1">/ 1Î∞ï</span>
              </div>

              <div className="space-y-4 mb-6">
                <div>
                  <label className="block text-xs font-bold text-gray-700 uppercase mb-1">Ï≤¥ÌÅ¨Ïù∏</label>
                  <input type="date" className="w-full p-2 border rounded-md" value={startDate} onChange={(e) => setStartDate(e.target.value)} />
                </div>
                <div>
                  <label className="block text-xs font-bold text-gray-700 uppercase mb-1">Ï≤¥ÌÅ¨ÏïÑÏõÉ</label>
                  <input type="date" className="w-full p-2 border rounded-md" value={endDate} onChange={(e) => setEndDate(e.target.value)} />
                </div>
              </div>

              <button
                onClick={handleOpenModal}
                disabled={processing || !startDate || !endDate}
                className={`w-full py-3 rounded-lg font-bold text-white transition-all shadow-md ${
                  processing || !startDate || !endDate ? 'bg-gray-400 cursor-not-allowed' : 'bg-gradient-to-r from-indigo-600 to-purple-600 hover:shadow-lg transform hover:-translate-y-0.5'
                }`}
              >
                {processing ? 'Ï≤òÎ¶¨ Ï§ë...' : 'ÏòàÏïΩ Î∞è Í≤∞Ï†úÌïòÍ∏∞'}
              </button>

              {startDate && endDate && (
                <div className="mt-6 pt-4 border-t border-gray-100">
                  <div className="flex justify-between mb-2 text-gray-600">
                    <span>{caravan.pricePerDay.toLocaleString()}Ïõê √ó {Math.ceil(Math.abs(new Date(endDate).getTime() - new Date(startDate).getTime()) / (1000 * 60 * 60 * 24))}Î∞ï</span>
                    <span>{totalPrice.toLocaleString()}Ïõê</span>
                  </div>
                  <div className="flex justify-between font-bold text-lg text-gray-900 mt-2 pt-2 border-t border-gray-100">
                    <span>Ï¥ù Ìï©Í≥Ñ</span>
                    <span>{totalPrice.toLocaleString()}Ïõê</span>
                  </div>
                </div>
              )}
            </div>
          </div>
        )}

      </div>
    </div>
  );
}